.trackerContainer {
  margin: 10px;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: max-content;
      grid-template-columns: -webkit-max-content;
      grid-template-columns: max-content;
  row-gap: 10px;
}

.trackerContainer .trackerComponentContainer {
  position: relative;
  padding: 10px;
  border: 1px solid darkgray;
}

.trackerContainer .trackerComponentContainer hr {
  height: 1px;
  border: 0px;
  border-top: 1px solid lightgray;
}

.trackerContainer .activeFiguresContainer .title {
  font-size: x-large;
  text-align: center;
}

.trackerContainer .activeFiguresContainer .menu {
  position: absolute;
  top: 4px;
  right: 4px;
  padding: 4px 8px;
}

.trackerContainer .activeFiguresContainer .trackedClassContainer {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: [turn-complete] max-content [info] fit-content(300px) [initiative] max-content [move-buttons] max-content [delete] max-content;
      grid-template-columns: [turn-complete] -webkit-max-content [info] fit-content(300px) [initiative] -webkit-max-content [move-buttons] -webkit-max-content [delete] -webkit-max-content;
      grid-template-columns: [turn-complete] max-content [info] fit-content(300px) [initiative] max-content [move-buttons] max-content [delete] max-content;
  place-items: center;
}

.trackerContainer .activeFiguresContainer .roundActionsOuter {
  display: -ms-grid;
  display: grid;
  -webkit-column-gap: 10px;
          column-gap: 10px;
  -ms-grid-columns: auto auto;
      grid-template-columns: auto auto;
  justify-items: center;
}

.trackerContainer .figureAdderContainer .title {
  font-size: large;
  text-align: center;
}

.trackerContainer .figureAdderContainer .collapseExpand {
  position: absolute;
  top: 8px;
  right: 4px;
  padding: 4px 8px;
  color: #646464;
}
